* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: white;
}

body {
  padding: 60px;
  background-color: #206391;
}

h2 {
  font-size: 2.2rem;
  text-align: center;
}

input {
  background-color: whitesmoke;
  font-size: 1.2rem;
  width: 100%;
  color: rgb(78, 78, 78);
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid rgb(185, 184, 184);
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  background-color:#6a8f9e;
  padding: 10px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  display:block;
  margin:auto;
  border: 1px solid gray;
}